.footer{
    width:100%;
    padding: 6rem 0;
    background-color: rgba(19,19,19,0.8);

   

}

.footer-container{
    max-width: 100%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap:40px;
}

.left{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    
}

.location{
    display: flex;
    margin-bottom: 1rem;
    align-items: center;

}
.location p{
    line-height: 30px;
}
.phone{
    margin-bottom: 1rem;
}

.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.social{
display: flex;
align-items: center;
margin-top: 1rem;
justify-content: center;

}
.right h4{

    margin:auto;
}
h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;

}

p{
    font-size: 1.2rem;
}

@media screen and (max-width:800px)
{
    .footer-container{
        grid-template-columns: 1fr;
    }
   .right p{
        font-size: 1rem;
    }
   
}